.leftSide{
    width: 252px;
    height: 990px;
    /*border: 1px solid black;*/
    display: flex;
    flex-direction: column;
    align-items: center;
}
.logo{
    background-image: url("./img/logo.svg");
    width: 93px;
    height: 44px;
    margin-top: 51px;
    cursor: pointer;
}
.explore{
    margin-top: 57px;
    font-style: normal;
    font-weight: 800;
    font-size: 30px;
    line-height: 47px;
}
.menuLi{
    height: 24px;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    margin-top: 25px;
    display: flex;
    padding: 0;
    cursor: pointer;
}
.menuLi p:hover{
    color: gray;
}
.menuLi:nth-of-type(1) .img{
    background-image: url("./img/newIn.svg");
    width: 23px;
    height: 23px;
    margin-right: 7px;
    background-repeat: no-repeat;
}
.menuLi:nth-of-type(2) .img{
    background-image: url("./img/Clothing.svg");
    width: 23px;
    height: 23px;
    margin-right: 7px;
    background-repeat: no-repeat;
}
.menuLi:nth-of-type(3) .img{
    background-image: url("./img/Shoes.svg");
    width: 23px;
