    margin-right: 7px;
    background-repeat: no-repeat;
}
.menuLi:nth-of-type(4) .img{
    background-image: url("./img/Accessories.svg");
    width: 23px;
    height: 23px;
    margin-right: 7px;
    background-repeat: no-repeat;
}
.menuLi:nth-of-type(5) .img{
    background-image: url("./img/Activewear.svg");
    width: 23px;
    height: 23px;
    margin-right: 7px;
    background-repeat: no-repeat;
}
.menuLi:nth-of-type(6) .img{
